import React from "react";
import { Route } from "react-router-dom";
import PickProcedure from "../pick-procedure-page/pick-procedure.component";
import PickTimePage from "../pick-time-page/pick-time-page.component";

const Book = ({ match }) => {
  return (
    <div className="book">
      <Route exact path={`${match.path}`} component={PickProcedure} />
      <Route exact path={`${match.path}/:procedure`} component={PickTimePage} />
    </div>
  );
};

export default Book;
