import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

import CustomButton from "../../components/custom-button/custom-button.component";
import FullWidthCard from "../../components/full-width-card/full-width-card.component";

import "./pick-procedure.styles.scss";

const PickProcedure = ({ match, procedures }) => (
  <div className="pick-procedure-page">
    <FullWidthCard>
      <div className="title-container">
        <p className="action-prompt">Book a session</p>
        <h2>Which service would you like to book a session for?</h2>
      </div>
      <div className="procedures">
        {procedures.map((procedure) => (
          <div className="procedure" key={procedure.id}>
            <h3 className="procedure-title">{procedure.title}</h3>
            <div className="procedure-details">
              ${procedure.cost} - {procedure.time} min
            </div>
            <Link
              to={`${match.path}/${procedure.title
                .toLowerCase()
                .replace(" ", "_")}`}
            >
              {/* <CustomButton>Pick</CustomButton> */}
            </Link>
          </div>
        ))}
      </div>
    </FullWidthCard>
  </div>
);

const mapStateToProps = (state) => ({
  procedures: state.procedures.procedures,
});

export default connect(mapStateToProps)(PickProcedure);
