import React from "react";

import "./pick-day.styles.scss";

const weekDays = [
  "Sunday",
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
];

class PickDay extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeDay: 0,
      days: [],
      formatter: new Intl.DateTimeFormat("bg"),
    };
  }

  componentDidMount() {
    this.setState({
      days: this.props.days.map((day) => new Date(day.date)),
    });
  }

  handleClick = (idx) => () => {
    this.setState({ activeDay: idx });
  };

  render() {
    return (
      <div className="pick-day">
        <div className="hint">Available times</div>
        <div className="days">
          {this.state.days.map((day, idx) => (
            <div
              className={`day-link ${
                idx === this.state.activeDay ? "active" : ""
              }`}
              key={day.getDate()}
              onClick={this.handleClick(idx)}
            >
              <div className="day-of-week">{weekDays[day.getDay()]}</div>
              <div className="date">{this.state.formatter.format(day)}</div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default PickDay;
