import React from "react";
import { connect } from "react-redux";

import Container from "../../components/container/container.component";
import PickDay from "../../components/pick-day/pick-day.component";
import "./pick-time-page.styles.scss";

class PickTimePage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "...",
    };
  }

  componentDidMount() {
    let title = this.props.match.params.procedure.replace("_", " ");
    title = title.charAt(0).toUpperCase() + title.slice(1);

    this.setState({ title });
  }

  handleDayChange = (event) => {};

  render() {
    return (
      <div className="pick-time-page">
        <Container>
          <div className="title-wrapper">
            <span className="action-prompt">Book a session for</span>
            <h2>{this.state.title}</h2>
          </div>

          <div className="available-times-wrapper">
            <PickDay days={this.props.days} onChange={this.handleDayChange} />
          </div>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  days: state.schedule.days,
});

export default connect(mapStateToProps)(PickTimePage);
