import React from "react";

import Container from "../../components/container/container.component";
import CustomButton from "../../components/custom-button/custom-button.component";
import FullWidthCard from "../../components/full-width-card/full-width-card.component";

import "./contact.styles.scss";

const ContactPage = () => (
  <div class="contact-page">
    <Container>
      <h2>Contact</h2>
    </Container>
    <FullWidthCard>
      <form>
        <label htmlFor="name">Your name</label>
        <input name="name" type="text" placeholder="Your name"></input>

        <label htmlFor="name">Your email</label>
        <input name="name" type="email" placeholder="Your email"></input>

        <label htmlFor="name">Message</label>
        <textarea name="name" placeholder="Message" rows="5"></textarea>

        <div style={{ paddingTop: "15px" }}>
          <CustomButton>Send</CustomButton>
        </div>
      </form>
    </FullWidthCard>
  </div>
);

export default ContactPage;
